.headerContainer {
  position: relative;
  height: 600px;
  background: #fffdfd;
  overflow: hidden;
  transition: 0.2s;
}
.headerContainer .background {
  position: absolute;
  width: 150vw;
  height: 100%;
  right: 0;
  background: linear-gradient(15deg, #ffd8d8, #cbe6ff);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  -webkit-transform: rotate(5deg);
          transform: rotate(5deg);
  transition: 0.2s;
  z-index: 0;
}
.headerContainer .userInfo {
  position: relative;
  display: flex;
  padding-top: 70px;
}
.headerContainer .photo {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: url(/my-react-resume/static/media/me.4bd586e0.jpg) center center no-repeat;
  background-size: cover;
  border: solid 6px #FFF;
  transition: 0.2s;
}
.headerContainer .titleArea {
  margin-left: 50px;
}
.headerContainer .title {
  color: #443163;
  letter-spacing: 1px;
  font-size: 200%;
  font-family: 微軟正黑體;
  transition: 0.2s;
}
.headerContainer .subtitle {
  margin: 5px 0 30px 0;
  color: #554b63;
  font-size: 115%;
  font-family: 微軟正黑體;
  font-weight: 500;
  transition: 0.2s;
}
.headerContainer .contact {
  position: relative;
  margin-top: 10px;
  padding-left: 70px;
  color: #554b63;
  font-size: 115%;
  font-family: 微軟正黑體;
  font-weight: 900;
  transition: 0.2s;
}
.headerContainer .contact.email::before {
  content: 'E-mail： ';
  position: absolute;
  left: 0;
  top: 0;
}
.headerContainer .contact.tel::before {
  content: 'Phone： ';
  position: absolute;
  left: 0;
  top: 0;
}
.headerContainer .links {
  display: flex;
  margin-top: 15px;
}
.headerContainer .links li {
  position: relative;
  display: flex;
  margin-right: 15px;
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  border-radius: 8px;
  /* border-radius: 50%; */
  cursor: pointer;
  transition: 0.2s;
}
.headerContainer .links li:hover {
  background: #554b63;
}
.headerContainer .links .github:hover {
  background: #807294;
}
.headerContainer .links .codepen:hover {
  background: #999;
}
.headerContainer .links .facebook:hover {
  background: #8097ff;
}
.headerContainer .links .instagram:hover {
  background: #ff94c6;
}
.headerContainer .links .youtube:hover {
  background: #f58383;
}
.headerContainer .links li:hover::before {
  color: #ffffff;
}
.headerContainer .links li::before {
  font-family: FontAwesome;
  font-size: 200%;
  color: #554b63;
  transition: 0.2s;
}
.headerContainer .links .github::before {
  content: '\f09b';
  font-size: 230%;
  color: #807294;
}
.headerContainer .links .codepen::before {
  content: '\f1cb';
  color: #999;
}
.headerContainer .links .facebook::before {
  content: '\f082';
  color: #8097ff;
}
.headerContainer .links .instagram::before {
  content: '\f16d';
  color: #ff94c6;
}
.headerContainer .links .youtube::before {
  content: '\f16a';
  color: #f58383;
}
.headerContainer .links a {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.headerContainer .titleBackground {
  position: absolute;
  font-size: 1300%;
  font-weight: 100;
  left: 40px;
  top: 350px;
  color: #cdc6da;
  transition: 0.2s;
}
.headerContainer .summary {
  margin-top: 40px;
  color: #69518f;
  transition: 0.2s;
}
.headerContainer .summary p {
  position: relative;
  max-width: 1500px;
  margin-top: 10px;
  padding-right: 100px;
  font-size: 160%;
  font-style: italic;
  letter-spacing: 2px;
  font-weight: 900;
  text-align: right;
  z-index: 1;
}

/* Dark Theme */
.headerContainer.dark {
  background: #2c2b2b;
}
.headerContainer.dark .background {
  background: linear-gradient(15deg, #332b2b, #424b53);
}
.headerContainer.dark .photo {
  border: solid 6px #9b9b9b;
}
.headerContainer.dark .title {
  color: #e2dede;
}
.headerContainer.dark .subtitle {
  color: #adadad;
}
.headerContainer.dark .contact {
  color: #00f7ff;
}
.headerContainer.dark .links li {
  background: #272525;
}
.headerContainer.dark .links li:hover {
  background: #424040;
  box-shadow: 0 0 5px #FFF;
}
.headerContainer.dark .links li:hover::before {
  color: #FFF !important;
}
.headerContainer.dark .links .github::before {
  color: #d7bbff;
}
.headerContainer.dark .links .codepen::before {
  color: #dacbcb;
}
.headerContainer.dark .links .facebook::before {
  color: #b9c6ff;
}
.headerContainer.dark .links .instagram::before {
  color: #ffbddc;
}
.headerContainer.dark .links .youtube::before {
  color: #fcb1b1;
}
.headerContainer.dark .titleBackground {
  color: #3f3f3f;
}
.headerContainer.dark .summary {
  color: #e2dede;
}

/* RWD */
@media screen and (max-width: 1500px) {
  .headerContainer {
    height: 540px;
  }
  .headerContainer .userInfo {
    padding-top: 63px;
  }
  .headerContainer .photo {
    width: 180px;
    height: 180px;
    border: solid 6px #FFF;
  }
  .headerContainer .titleArea {
    margin-left: 40px;
  }
  .headerContainer .title {
    font-size: 180%;
  }
  .headerContainer .subtitle {
    margin: 4px 0 27px 0;
    font-size: 103%;
  }
  .headerContainer .contact {
    position: relative;
    margin-top: 9px;
    padding-left: 63px;
    font-size: 103%;
  }
  .headerContainer .links {
    margin-top: 12px;
  }
  .headerContainer .links li {
    margin-right: 12px;
    width: 36px;
    height: 36px;
  }
  .headerContainer .links li::before {
    font-size: 180%;
  }
  .headerContainer .links .github::before {
    font-size: 210%;
  }
  .headerContainer .titleBackground {
    font-size: 1170%;
    left: 36px;
    top: 315px;
    color: #cdc6da;
    transition: 0.2s;
  }
  .headerContainer .summary {
    margin-top: 36px;
  }
  .headerContainer .summary p {
    max-width: 1350px;
    margin-top: 9px;
    padding-right: 90px;
    font-size: 144%;
    font-style: italic;
    letter-spacing: 2px;
    font-weight: 900;
    text-align: right;
    z-index: 1;
  }
}

.experienceContainer {
  position: relative;
  padding: 70px 0 150px 0;
  background: #fffdfd;
  overflow: hidden;
  transition: 0.2s;
}
.experienceContainer .background {
  position: absolute;
  width: 150vw;
  height: 100%;
  right: 0;
  top: calc(100% - 250px);
  background: #ffeaea;
  -webkit-transform-origin: top right;
          transform-origin: top right;
  -webkit-transform: rotate(-5deg);
          transform: rotate(-5deg);
  transition: 0.2s;
  z-index: 0;
}
.experienceContainer .titleBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.experienceContainer .title {
  position: relative;
  margin-bottom: 10px;
  font-size: 200%;
}
.experienceContainer .title::before,
.experienceContainer .title::after {
  content: '';
  position: absolute;
  width: 200px;
  height: 1px;
  bottom: -5px;
  background: #554b63;
  transition: 0.2s;
}
.experienceContainer .title::before {
  -webkit-transform: translateX(calc(-100% - 50px));
          transform: translateX(calc(-100% - 50px));
}
.experienceContainer .title::after {
  -webkit-transform: translateX(calc(0% + 50px));
          transform: translateX(calc(0% + 50px));
}
.experienceContainer .subtitle {
  font-size: 150%;
  font-style: italic;
}
.experienceContainer .title,
.experienceContainer .subtitle {
  color: #554b63;
  letter-spacing: 1px;
  transition: 0.2s;
}
.experienceContainer .timelineBlock {
  display: flex;
  margin-top: 100px;
  justify-content: center;
}
.experienceContainer .timeline {
  padding: 0 0 0 200px;
}
.experienceContainer .timeline .item {
  position: relative;
  padding-bottom: 50px;
  font-size: 150%;
  color: #554b63;
  font-weight: 900;
  font-family: 微軟正黑體;
  transition: 0.2s;
}
.experienceContainer .timeline .item::before,
.experienceContainer .timeline .item::after {
  content: '';
  position: absolute;
  border-radius: 50%;
}
.experienceContainer .timeline .item::before {
  width: 16px;
  height: 16px;
  top: 7px;
  left: -50px;
  background: #e5d2ff;
  transition: 0.2s;
  z-index: 1;
  -webkit-animation: linePoint 3s infinite;
          animation: linePoint 3s infinite;
}
.experienceContainer .timeline .item::after {
  width: 16px;
  height: 16px;
  top: 7px;
  left: -50px;
  background: #554b63;
  transition: 0.2s;
  z-index: 1;
}
.experienceContainer .timeline .item:last-child .line {
  display: none;
}
.experienceContainer .timeline .year {
  position: absolute;
  top: 1px;
  right: calc(100% + 70px);
  font-size: 90%;
  white-space: nowrap;
  font-weight: 900;
}
.experienceContainer .timeline .line {
  position: absolute;
  top: 35px;
  left: -42px;
  width: 1px;
  height: calc(100% - 40px);
  background: #554b63;
  transition: 0.2s;
  opacity: 0.5;
  z-index: 0;
}
.experienceContainer .timeline .unit {
  margin-top: 15px;
  font-size: 80%;
  color: #69518f;
  transition: 0.2s;
}
.experienceContainer .timeline .works {
  margin-top: 5px;
  max-width: 380px;
  font-size: 73%;
  color: #bd5b00;
  text-align: justify;
  font-weight: 400;
  transition: 0.2s;
}

/* Dark Theme */
.experienceContainer.dark {
  background: #2c2b2b;
}
.experienceContainer.dark .background {
  background: #291e1e;
}
.experienceContainer.dark .title::before,
.experienceContainer.dark .title::after,
.experienceContainer.dark .timeline .item::after,
.experienceContainer.dark .timeline .line {
  background: #c7c7c7;
}
.experienceContainer.dark .title,
.experienceContainer.dark .subtitle,
.experienceContainer.dark .timeline .item {
  color: #c7c7c7;
}
.experienceContainer.dark .timeline .item::before {
  background: #7a7777;
}
.experienceContainer.dark .timeline .unit {
  color: #67cdfd;
}
.experienceContainer.dark .timeline .works {
  color: #f7a354;
}

/* Animations */
@-webkit-keyframes linePoint {
  0% {
    opacity: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  30% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(2);
            transform: scale(2);
  }
}
@keyframes linePoint {
  0% {
    opacity: 0;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  30% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: scale(2);
            transform: scale(2);
  }
}
