.sidebarContainer .miniButtonGroup .link.codepen:hover {
  background: #b9b9b9;
}
.sidebarContainer .miniButtonGroup .link.facebook:hover {
  background: #9badff;
}
.sidebarContainer .miniButtonGroup .link.instagram:hover {
  background: #ffadd3;
}
.sidebarContainer .miniButtonGroup .link.youtube:hover {
  background: #ffaaaa;
}
/* Mini Buttons ::before */
.sidebarContainer .miniButtonGroup .link.github::before {
  content: '\f09b';
}
.sidebarContainer .miniButtonGroup .link.codepen::before {
  content: '\f1cb';
}
.sidebarContainer .miniButtonGroup .link.facebook::before {
  content: '\f082';
}
.sidebarContainer .miniButtonGroup .link.instagram::before {
  content: '\f16d';
}
.sidebarContainer .miniButtonGroup .link.youtube::before {
  content: '\f16a';
}
.sidebarContainer .miniButtonGroup.active .link {
  opacity: 1;
  pointer-events: auto;
}
.sidebarContainer .miniButtonGroup.active .link:nth-child(2) {
  -webkit-transform: translate(70px, -50%) rotate(-60deg);
          transform: translate(70px, -50%) rotate(-60deg);
}
.sidebarContainer .miniButtonGroup.active .link:nth-child(3) {
  -webkit-transform: translate(70px, -50%) rotate(-30deg);
          transform: translate(70px, -50%) rotate(-30deg);
}
.sidebarContainer .miniButtonGroup.active .link:nth-child(4) {
  -webkit-transform: translate(70px, -50%) rotate(0deg);
          transform: translate(70px, -50%) rotate(0deg);
}
.sidebarContainer .miniButtonGroup.active .link:nth-child(5) {
  -webkit-transform: translate(70px, -50%) rotate(30deg);
          transform: translate(70px, -50%) rotate(30deg);
}
.sidebarContainer .miniButtonGroup.active .link:nth-child(6) {
  -webkit-transform: translate(70px, -50%) rotate(60deg);
          transform: translate(70px, -50%) rotate(60deg);
}
.sidebarContainer .miniButtonGroup.active .link:nth-child(2)::before {
  -webkit-transform: rotate(60deg);
          transform: rotate(60deg);
}
.sidebarContainer .miniButtonGroup.active .link:nth-child(3)::before {
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}
.sidebarContainer .miniButtonGroup.active .link:nth-child(4)::before {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
.sidebarContainer .miniButtonGroup.active .link:nth-child(5)::before {
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
}
.sidebarContainer .miniButtonGroup.active .link:nth-child(6)::before {
  -webkit-transform: rotate(-60deg);
          transform: rotate(-60deg);
}
.sidebarContainer .navList {
  height: calc(100% - 100px);
  margin-top: 10px;
  overflow-y: auto;
  transition: 0.2s;
}
.sidebarContainer .navItem {
  /* background: #e0deec; */
  position: relative;
  padding: 10px 10px 10px 45px;
  margin-bottom: 10px;
  border-radius: 10px;
  font-size: 120%;
  cursor: pointer;
  color: #443163;
  overflow: hidden;
  transition: 0.1s;
}
.sidebarContainer .navItem:hover,
.sidebarContainer .navItem.active {
  background: #e0deec;
}
.sidebarContainer .navItem:hover::before,
.sidebarContainer .navItem.active::before {
  opacity: 1;
}
.sidebarContainer .navItem:hover .navItemDetail {
  margin-top: 5px;
  max-height: 50px;
  opacity: 1;
  /* transition: margin-top 0. */
}
.sidebarContainer .navItem::before {
  position: absolute;
  height: 25px;
  width: 25px;
  top: 9px;
  left: 10px;
  font-family: FontAwesome;
  text-align: center;
  line-height: 25px;
  font-size: 130%;
  opacity: 0.5;
  transition: 0.1s;
}
.sidebarContainer .navItem.basicInfo::before {
  content: '\f007';
}
.sidebarContainer .navItem.experience::before {
  content: '\f19d';
  font-size: 115%;
  padding-left: 1px;
}
.sidebarContainer .navItem.skills::before {
  content: '\f0ad';
  font-size: 125%;
}
.sidebarContainer .navItem.about::before {
  content: '\f1ae';
}
.sidebarContainer .navItem.autobiography::before {
  content: '\f200';
  font-size: 115%;
  padding-left: 1px;
}
.sidebarContainer .navItem.works::before {
  content: '\f109';
  font-size: 125%;
  padding-left: 1px;
}
.sidebarContainer .navItem.resume::before {
  content: '\f2c2';
  font-size: 115%;
  padding-left: 1px;
}
.sidebarContainer .navItemDetail {
  font-size: 80%;
  margin-top: 0px;
  max-height: 0px;
  word-break: keep-all;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.2s 0.4s, margin-top 0.2s 0.4s, max-height 0.2s 0.4s;
}
.sidebarContainer .toggleSwitch {
  position: absolute;
  display: flex;
  width: 60px;
  height: 30px;
  bottom: 30px;
  left: 50%;
  padding: 0 5px 1px 5px;
  justify-content: space-between;
  align-items: center;
  background: #f8f6fc;
  border-radius: 50px;
  cursor: pointer;
  overflow: hidden;
  box-shadow: inset 0 0 3px #acacac;
  transition: 0.2s;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.sidebarContainer .toggleSwitch::before,
.sidebarContainer .toggleSwitch::after {
  font-family: FontAwesome;
  font-size: 150%;
}
.sidebarContainer .toggleSwitch::before {
  content: '\f185';
  color: #e4e4e4;
  text-shadow: 0 0 3px #ffffff;
}
.sidebarContainer .toggleSwitch::after {
  content: '\f186';
  color: #bda000;
  text-shadow: 0 0 3px #ffd900;
}
.sidebarContainer input {
  display: none;
}
.sidebarContainer input:checked ~ .toggle {
  -webkit-transform: translateX(30px);
          transform: translateX(30px);
}
.sidebarContainer .toggle {
  position: absolute;
  width: 28px;
  height: 28px;
  top: 1px;
  left: 1px;
  border-radius: 50%;
  background: #FFF;
  box-shadow: 0 0 5px #808080;
  pointer-events: none;
  transition: 0.2s;
  z-index: 1;
}
.sidebarContainer .handleClick {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50px;
  top: 0;
  left: 0;
  background: transparent;
}

/* Dark Theme */
